<template>
  <div class="index" ref="indexDiv">
    <div class='top_header flex justify_between'>
      <div><!--{{'<'}}--></div>
      <div>我的考试</div>
      <div  > </div>
		<div class="iconSvg"> <icon-svg iconClass="usericon" ></icon-svg>{{userName}}{{workNumber?`(${workNumber})`:''}}</div>

	</div>
     <div class="content">
       <SearchTable   class="fistTable"   :data="tableData" :tableConfig="tableConfig"
                     @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange">
         <el-table-column   slot="option" label="操作" align="center" :width="80"  >
           <template  slot-scope="{ row }">
             <el-tooltip   effect="dark" :enterable="false" content="参加考试" placement="top">
                                 <span @click="lineTest('/onlineTestDo',row)" class="rowSvg item">
                                        <icon iconClass="exam"  ></icon>
                                    </span>
             </el-tooltip>

           </template>
         </el-table-column>
       </SearchTable>

    </div>
  </div>
</template>
<script>
  import Icon from "@/components/Icon-svg/index";

  import {getUserInfo } from '@/lib/auth';
  import SearchTable from '@/components/Ftable/index';
  import { lineTestConfig } from '../tableConfig.js';
  import request from '@/lib/axios.js';
  import {  extend } from 'lodash';
  export default {
    components:{SearchTable,Icon},
    data(){
      return {
		  userName:getUserInfo().name,
		  workNumber:getUserInfo().workNumber,
         tableData:[],
         tableConfig:lineTestConfig(),
        params:{
          current: 1,
          size: 15,
        },
      }
    },
    created(){

      this.getList()
    },
    methods:{
      lineTest(path,row){

        if(row.examTime>new Date().getTime()- 8.64e7){

          this.$router.push({
            path:path,
            query:{id:row.examId}
          })
        }else{
          this.$message.info('考试已过期，下次请提前参加')
        }

      },
      getList(){
        let data={
          employeeId:getUserInfo().id
        }
          request({
          url:`${this.$ip}/mms-training/examLine/list`,
          method: 'post',
          data:data,
          params:{...this.params,}
        })
          .then((data) => {
            console.log(11,data);
            if(data.code==200){
              data.data.records.map((k,l)=>{
                k.id=l+1
              })
              this.tableData = extend({},
                {...data.data}
              );
            }
          })
      },


      handleSizeChange(size) {
        this.params.current = 1;
        this.params.size = size;
        this.getList()
      },
      handleCurrentChange(current) {
        this.params.current = current;
        this.getList()
      },
    },

  }
</script>
<style lang="less" scoped>
  .index {
    height:calc(100vh - 69px);
    .content{
      padding: 10px 20px 20px 20px ;
    }
    .tableTitle{
      text-align: center;
      font-weight: bold;
      margin: 10px 0;
    }
    .secondTable,.fistTable{
      /deep/ .el-table__body-wrapper{
        height: calc(100vh - 260px) !important;
        overflow-y: auto;
      }
    }
    .top_header {
      line-height: 68px;
      height: 68px;
      font-size: 24px;

      background: #2D4157;
      color: #fff;
      padding: 0 10px;
    }

    .rowSvg{
      .el-button{
        padding:5px 10px;
      }
    }

  }
</style>



